@mixin hover-transition {
  transition: 0.2s all;
}

.btn,
.btn-outline {
  font-family: $heading-font;
  letter-spacing: 1px;
  padding: 1.2rem 1.5rem;
  margin: 1rem auto;
  border: 2px solid;
  text-transform: uppercase;
  @include hover-transition;
}

.btn {
  background-color  : $accent-color;
  color             : $light-color;
  border-color      : inherit;
  &:hover,
  &:focus-within {
    background-color: lighten($accent-color, 10);
  }
}

.btn-outline--light {
  color         : $light-color;
  border-color  : inherit;
  &:hover,
  &:focus-within {
    background-color  : $accent-color;
    border-color      : $accent-color;
  }
}

.btn-outline--dark {
  border-color: $accent-color;
  color: $accent-color;
  &:hover,
  &:focus-within {
    color: $light-color;
  }
}

// Chat bubble
$size: 40px;

#chat-bubble {
  position: fixed;
  bottom: 2.5rem;
  right: 2.5rem;
  height: $size;
  width: $size;
  margin: auto;
  border: none;
  outline: none;
  transition: 0.2s ease-in-out;
  z-index: 1001;
  svg {
    fill: $dark-color;
  }
  cursor: pointer;
}

#chat-bubble::before {
  content   : '';
  position  : absolute;
  top       : 50%;
  left      : 50%;
  transform : translate(-50%, -50%);
  width     : calc(#{$size} * 1.8);
  height    : calc(#{$size} * 1.8);
  border-radius   : 50%;
  background-color: whitesmoke;
  box-shadow      : 5px 5px 10px rgba(30, 30, 30, 0.5);
  z-index         : -1;
}
#chat-bubble.active {
  svg {
    fill    : whitesmoke;
  }
}
#chat-bubble.active::before {
  background    : rgb(3,158,173);
  background    : linear-gradient(159deg, rgba(3,158,173,1) 0%, rgba(9,76,121,1) 35%, rgba(255,0,183,1) 100%);
}

.chat-menu {
  position    : absolute;
  bottom      : 100px;
  right       : -50%;
  margin-bottom : 0;
  transform   : translateX(200px);
  transition  : 0.2s ease-in-out;
  text-transform  : uppercase;
  font-size : 1rem;
  color : $accent-color;
  background-color  : whitesmoke;
  border-radius : 20px;
  padding : 15px;
}
.chat-menu.open {
  transform : translateX(0);
  transition: 0.2s ease-in-out;
}

.chat-menu-item {
  list-style  : none;
}
.chat-menu-item--icon {
  width     : $size;
  padding   : 5px;
  max-width : unset;
  height    : auto;
  margin    : auto;
  margin-bottom: 1.5rem;
  &:last-of-type {
    margin-bottom: 0;
  }
}

